html {
  height: 200vh;
  overflow-y: scroll;
  padding: 1vw;
}

body,
#root {
  height: 100%;
}

.App {
  display: flex;
  height: 100%;
  flex-wrap: wrap;
  margin: 0;
}

#game-canvas,
#slide-deck {
  flex: 1;
}

.frame-container {
  flex: 1;
  display: flex;
  padding-bottom: 1vw;
}

.tailspin-loader {
  align-items: center;
  justify-content: center;
  min-width: 100%;
  min-height: 100%;
  background-color: rgb(20, 20, 20);
}

.break {
  flex-basis: 100%;
  height: 0;
}
